.info {
    width: 90%;
    margin: 0 auto;
    .login_header {
        padding: 0.25rem 0;
    }
    .login_txt {
        margin: 0.2rem 0 0.5rem;
        p {
            margin-top: 0.2rem;
            line-height: 0.5rem;
        }
    }
    .login_inp {
        p{
            font-size: 0.28rem;
            color: #000;
            line-height: 0.5rem;
            margin-top: 0.5rem;
        }
        input {
            border-bottom: 2px solid #008489;
        }
    }
}